import React, { Component } from "react";
import { connect } from "react-redux";
import { Auth, Basic } from "../../../actions";
import { StoreState } from "../../../reducers";

class _PostsRequestsReport extends Component {
  render() {
    return <div>PostsRequestsReport</div>;
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const PostsRequestsReport = connect(
  mapStateToProps,
  {}
)(_PostsRequestsReport);
